import * as tf from '@tensorflow/tfjs';
import * as tfvis from '@tensorflow/tfjs-vis';
import { TFProject } from '..';
import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { NVector3, MathUtility } from '../../../lib';

import { TFDataUtility } from '../../../../../../lib/NJSCore/ML/NMacineLearning';
// // https://editor.p5js.org/codingtrain/sketches/UdXRZxF7e
export class TFModel01HorsepowerAndMilesPerGallon extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];
    selVec: NVector3 | undefined = undefined;

    // tfModel: ModelForHorsepowerGallon;

    constructor(tfProjct: TFProject, width: number = 650, height: number = 350) {
        super(tfProjct, width, height);
        this.indeCanvas.title = 'TF Horsepower and Miles Per Gallon';
        this.Init();

    }
    Init() {
        // console.log(tf.version);

        TFDataUtility.getData().then((d: any) => {
            // this.tfModel = new ModelForHorsepowerGallon();
            // this.train(d);
            this.train(d);


        });

    }
    async train(d: any) {
        const tensorData = convertToTensor(d);
        // console.log(tensorData);
        const { inputs, labels } = tensorData;
        // console.log(inputs, labels);
        const model = createModel();

        tfvis.show.modelSummary({ name: 'Model Summary' }, model);
        // Train the model
        await trainModel(model, inputs, labels);
        // console.log('Done Training');
        testModel(model, d, tensorData);
    }
    MouseDown(x: number, y: number) {
        const m = new NVector3(this.MapX(x), this.MapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            if (NVector3.Distance(m, this.vs[i]) < 0.05) {
                this.selVec = this.vs[i];
                return;
            }
        }
        this.vs.push(new NVector3(this.MapX(x), this.MapY(y)));
        // console.log(this.vs);
    }
    MouseDoubleClick() {
        // console.log(this.tfLinearRegression.dim);
        // this.tfLinearRegression.dim++;
    }
    MouseMove(x: number, y: number) {
        let selVec: undefined | NVector3 = undefined;
        const m = new NVector3(this.MapX(x), this.MapY(y));
        for (let i = 0; i < this.vs.length; ++i) {
            let theDis = NVector3.Distance(m, this.vs[i]);
            if (theDis < 0.05) {
                selVec = this.vs[i];
            }
        }
        if (selVec) {
            this.selVec = selVec;
        } else {
            this.selVec = undefined;
        }
    }
    MouseDrag(x: number, y: number) {
        if (this.selVec) {
            this.selVec.x = this.MapX(x);
            this.selVec.y = this.MapY(y);
        }
    }
    MouseUp() {
        this.selVec = undefined;
    }
    MapX(v: number) {
        return MathUtility.Remap(v, 0, this.width, -1, 1);
    }
    MapY(v: number) {
        return MathUtility.Remap(v, 0, this.height, 1, -1);
    }
    GridLine(ctx: CanvasRenderingContext2D) {
        ctx.strokeStyle = '#555555';
        const xInterval = this.width / 20 - 1;
        const yInterval = this.height / 20 - 1;
        for (let i = 0; i < 20; i += 1) {
            if (i === 10) {
                ctx.lineWidth = 3;
            } else {
                ctx.lineWidth = 1;
            }
            let x0 = xInterval * i;
            let y0 = 0;
            let x1 = x0;
            let y1 = this.height;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();

            x0 = 0;
            y0 = yInterval * i;
            x1 = this.width;
            y1 = y0;
            ctx.beginPath();
            ctx.moveTo(x0, y0);
            ctx.lineTo(x1, y1);
            ctx.stroke();
        }
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        // ctx.translate(ctx.canvas.width * 0.5, ctx.canvas.height * 0.5);

        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        this.GridLine(ctx);

        // this.GradientDescent();
        if (this.vs.length > 1) {
            const xx = [], yy = [];
            for (let i = 0; i < this.vs.length; ++i) { xx.push(this.vs[i].x); yy.push(this.vs[i].y); }
            // console.log(xx);
            // console.log(yy);
            // ............................ train
            // this.tfModel.train(xx, yy);
            // ............................ end train

            this.RenderLine(ctx);
        }
        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < this.vs.length; ++i) {
            ctx.beginPath();
            ctx.arc(
                MathUtility.Remap(this.vs[i].x, -1, 1, 0, this.width),
                MathUtility.Remap(this.vs[i].y, -1, 1, this.height, 0),
                5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }

        if (this.selVec) {
            ctx.fillStyle = '#ffffff';
            ctx.font = '15px Arial';
            ctx.textAlign = 'center';
            ctx.fillText(`x:${this.selVec.x.toFixed(3)}, y${this.selVec.y.toFixed(3)}`,
                MathUtility.Remap(this.selVec.x, -1, 1, 0, this.width),
                MathUtility.Remap(this.selVec.y, -1, 1, this.height, 0) - 9,
            );
        }

    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        const curveX: number[] = [];
        for (let x = -1; x <= 1; x += 0.05) {
            curveX.push(x);
        }
    }
}

function testModel(model: tf.Sequential, inputData: any, normalizationData: any) {
    const { inputMax, inputMin, labelMin, labelMax } = normalizationData;

    // Generate predictions for a uniform range of numbers between 0 and 1;
    // We un-normalize the data by doing the inverse of the min-max scaling
    // that we did earlier.
    const [xs, preds] = tf.tidy(() => {

        const xs = tf.linspace(0, 1, 100);
        const preds = model.predict(xs.reshape([100, 1])) as any;

        const unNormXs = xs
            .mul(inputMax.sub(inputMin))
            .add(inputMin);

        const unNormPreds = preds.mul(labelMax.sub(labelMin))
            .add(labelMin);

        // Un-normalize the data
        return [unNormXs.dataSync(), unNormPreds.dataSync()];
    });


    const predictedPoints = Array.from(xs).map((val, i) => {
        return { x: val, y: preds[i] };
    });

    const originalPoints = inputData.map((d: any) => ({
        x: d.horsepower, y: d.mpg,
    }));

    tfvis.render.scatterplot(
        { name: 'Model Predictions vs Original Data' },
        { values: [originalPoints, predictedPoints], series: ['original', 'predicted'] },
        {
            xLabel: 'Horsepower',
            yLabel: 'MPG',
            height: 300
        }
    );
}


async function trainModel(model: tf.Sequential, inputs: tf.Tensor<tf.Rank>, labels: tf.Tensor<tf.Rank>) {
    // Prepare the model for training.
    model.compile({
        optimizer: tf.train.adam(),
        loss: tf.losses.meanSquaredError,
        metrics: ['mse'],
    });

    const batchSize = 32;
    const epochs = 50;

    return await model.fit(inputs, labels, {
        batchSize,
        epochs,
        shuffle: true,
        callbacks: tfvis.show.fitCallbacks(
            { name: 'Training Performance' },
            ['loss', 'mse'],
            { height: 200, callbacks: ['onEpochEnd'] }
        )
    });
}


export const createModel = () => {
    // Create a sequential model
    const model = tf.sequential();
    // Add a single hidden layer
    model.add(tf.layers.dense({
        inputShape: [1],
        units: 16,
        useBias: true
    }));
    // Add an output layer
    model.add(tf.layers.dense({
        units: 1,
        useBias: true
    }));
    return model;
};
export const convertToTensor = (data: any) => {
    // Wrapping these calculations in a tidy will dispose any
    // intermediate tensors.

    return tf.tidy(() => {
        // Step 1. Shuffle the data
        tf.util.shuffle(data);

        // Step 2. Convert data to Tensor
        const inputs = data.map((d: any) => d.horsepower);
        const labels = data.map((d: any) => d.mpg);

        const inputTensor = tf.tensor2d(inputs, [inputs.length, 1]);
        const labelTensor = tf.tensor2d(labels, [labels.length, 1]);

        // Step 3. Normalize the data to the range 0 - 1 using min-max scaling
        const inputMax = inputTensor.max();
        const inputMin = inputTensor.min();
        const labelMax = labelTensor.max();
        const labelMin = labelTensor.min();

        const normalizedInputs = inputTensor.sub(inputMin).div(inputMax.sub(inputMin));
        const normalizedLabels = labelTensor.sub(labelMin).div(labelMax.sub(labelMin));

        return {
            inputs: normalizedInputs,
            labels: normalizedLabels,
            // Return the min/max bounds so we can use them later.
            inputMax,
            inputMin,
            labelMax,
            labelMin,
        };
    });
};