import * as tf from '@tensorflow/tfjs';
import { TFProject } from '..';
import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { NVector3, MathUtility } from '../../../lib';

// import { TFUtility } from '../../../../../../NJSCore/ML/NMacineLearning';
import { TFDataUtility } from '../../../../../../lib/NJSCore/ML/NMacineLearning';

export class TFLinearRegressionGradientDescent01 extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];
    vsTemp: NVector3[] = [];

    tfLinearRegression: TFLinearRegression;
    t: number = 0.0;
    constructor(tfProjct: TFProject) {
        super(tfProjct);
        this.indeCanvas.title = 'TF LinearRegression GradientDescent 01';
        this.Init();
    }
    Init() {
        // console.log(tf.version);

        this.tfLinearRegression = new TFLinearRegression();

        this.InitData();
        this.InitUI();
    }
    public InitUI() {
        const div = document.createElement('div');
        // div.style.background = '#dddddd';
        div.style.position = 'absolute';
        div.style.left = '400px';
        div.style.color = '#ffffff';

        const t = document.createElement('p');
        t.textContent = 'click on this screen to add data';
        div.append(t);

        const divHost = document.getElementById('indePanel');
        if (divHost) {
            divHost.append(div);
        }
        // this.scene.canvas.canvas.
    }
    InitData() {
        TFDataUtility.getData().then((d: any) => {
            let xMin = Number.MAX_SAFE_INTEGER;
            let xMax = Number.MIN_SAFE_INTEGER;
            let yMin = Number.MAX_SAFE_INTEGER;
            let yMax = Number.MIN_SAFE_INTEGER;

            d.map((data: any) => {
                let x = (data['horsepower']);
                if (x > xMax) { xMax = x; }
                if (x < xMin) { xMin = x; }

                let y = (data['mpg']);
                if (y > yMax) { yMax = y; }
                if (y < yMin) { yMin = y; }
            });

            for (let data of d) {
                let x = (data['horsepower']);
                let y = (data['mpg']);
                this.vsTemp.push(new NVector3(
                    MathUtility.Remap(x, xMin, xMax, 0.1, 0.9),
                    MathUtility.Remap(y, yMin, yMax, 0.1, 0.9)));
            }
        });
    }
    MouseDown(x: number, y: number) {
        this.vs.push(new NVector3(this.MapX(x), this.MapY(y)));
        // console.log(this.vs);
    }
    MapX(v: number) {
        return MathUtility.Remap(v, 0, this.width, 0, 1);
    }
    MapY(v: number) {
        return MathUtility.Remap(v, 0, this.height, 1, 0);
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        if ((this.t & 2) === 0) {
            const v = this.vsTemp.pop();
            if (v) {
                this.vs.push(v);
            }
        }

        // this.GradientDescent();
        if (this.vs.length > 1) {
            const xx = [], yy = [];
            for (let i = 0; i < this.vs.length; ++i) { xx.push(this.vs[i].x); yy.push(this.vs[i].y); }

            // ............................ train
            this.tfLinearRegression.train(xx, yy);
            // ............................ end train

            this.RenderLine(ctx);
        }

        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < this.vs.length; ++i) {
            ctx.beginPath();
            ctx.arc(MathUtility.Remap(this.vs[i].x, 0, 1, 0, this.width),
                MathUtility.Remap(this.vs[i].y, 0, 1, this.height, 0), 5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
        this.t += 0.3;
    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        const lineX = [0, 1];

        // ............................ predict
        const ys = tf.tidy(() => {
            return this.tfLinearRegression.predict(tf.tensor1d(lineX));
        });

        let lineY = ys.dataSync();
        ys.dispose();
        // ............................ end predict

        let x0 = 0;
        let y0 = lineY[0];

        let x1 = 1;
        let y1 = lineY[1];

        ctx.lineWidth = 3;
        ctx.strokeStyle = '#00fff0';
        ctx.beginPath();
        ctx.moveTo(MathUtility.Remap(x0, 0, 1, 0, this.width), MathUtility.Remap(y0, 0, 1, this.height, 0));
        ctx.lineTo(MathUtility.Remap(x1, 0, 1, 0, this.width), MathUtility.Remap(y1, 0, 1, this.height, 0));
        ctx.stroke();

    }
}
class TFLinearRegression {
    weight: tf.Variable;
    bias: tf.Variable;

    leraningRate: number = 0.5;
    optimizer: tf.Optimizer;
    constructor() {
        this.weight = tf.variable(tf.scalar(Math.random()));
        this.bias = tf.variable(tf.scalar(Math.random()));

        this.optimizer = tf.train.sgd(this.leraningRate);
    }
    public predict(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        return x.mul(this.weight).add(this.bias);
    }
    public loss(pred: tf.Tensor<tf.Rank>, label: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        return pred.sub(label).square().mean();
    }
    public train(xVal: number[], yVal: number[]) {

        tf.tidy(() => {
            const xs = tf.tensor1d(xVal);
            const ys = tf.tensor1d(yVal);
            this.optimizer.minimize((): any => this.loss(this.predict(xs), ys));
        });
        // console.log(tf.memory().numTensors);
        // console.log('b:', this.bias, ', w:', this.weight);
        // this.bias.print();
        // this.weight.print();
    }
    // public train(x: tf.Tensor, y: tf.Tensor) {
    //     tf.tidy((): any => {
    //         return this.optimizer.minimize((): any => {
    //             return this.loss(this.predict(x), y);
    //         });
    //     });
    // }
}