import { Canvas } from '../../Model/GraphicsCoreWrapper/CanvasWrapper/Canvas/Canvas';
import { ActiveCommandController } from './Core/ActiveCommandController';

import { DefinitionIndeCanvasCore } from './Core/DefinitionIndeCanvasCore';
import { JSLinearRegression01 } from './Definition/JSLinearRegression01';
import { JSLinearRegressionGradientDescent02 } from './Definition/JSLinearRegressionGradientDescent02';

import { TFLinearRegressionGradientDescent01 } from './Definition/TFLinearRegressionGradientDescent01';
import { TFPolynomialRegressionGradientDescent02 } from './Definition/TFPolynomialRegressionGradientDescent02';
import { TFPolynomialRegressionGradientDescent03Interaction } from './Definition/TFPolynomialRegressionGradientDescent03Interaction';
import { TFModel01 } from './Definition/TFModel01';
import { TFModel02XOR } from './Definition/TFModel02XOR';
import { TFModel03Iris } from './Definition/TFModel03Iris';
import { TFModel03SmartDrawing } from './Definition/TFModel03SmartDrawing';

import { TFModel01HorsepowerAndMilesPerGallon } from './Definition/TFModel01HorsepowerAndMilesPerGallon';
import { TFMnist } from './Definition/TFMnist';

import { Blank } from './Definition/Blank';
import { Implementation } from '..';
export class TFProject {
    implementation: Implementation;
    canvas: Canvas;
    activeCommandController: ActiveCommandController;

    definitions: DefinitionIndeCanvasCore[] = [];

    constructor(implementation: Implementation) {
        this.implementation = implementation;
        this.canvas = implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas;

        this.activeCommandController = new ActiveCommandController(this);
        this.Init();
        this.InitDefinition();
        this.InitDefault();
        this.InitPullDown();
    }
    public Init() {
        this.definitions = [];
    }
    get width() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.width;
    }
    get height() {
        return this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.canvas.height;
    }
    InitDefinition() {
        for (let d of this.definitions) { d.Init(this.implementation.app.model.graphicsCoreWrapper.canvasWrapper.canvas.ctx); }
    }
    Render(ctx: CanvasRenderingContext2D) {
        // for (let d of this.definitions) {d.Render(ctx); }
    }
    MouseMove(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMovePre(x, y); }
    }
    MouseLeftClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseLeftClickPre(x, y); }
    }
    MouseDown(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDownPre(x, y); }
    }
    MouseUp(x: number, y: number) {
        for (let d of this.definitions) { d.MouseUpPre(x, y); }
    }
    MouseDrag(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDragPre(x, y); }
    }
    MouseMiddleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseMiddleClickPre(x, y); }
    }
    MouseRightClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseRightClickPre(x, y); }
    }
    MouseDoubleClick(x: number, y: number) {
        for (let d of this.definitions) { d.MouseDoubleClickPre(x, y); }
    }
    MouseWheel(x: number, y: number, v: number) {
        for (let d of this.definitions) { d.MouseWheelPre(x, y, v); }
    }
    Dispose() {
        this.definitions = [];
    }
    InitDefault() {
        // new TFModel03SmartDrawing(this);

    }
    LoadDefinition(v: string) {
        // console.log('project code', v);
        if (v === 'blank') { new Blank(this); }

        // ...............................................................................

        if (v === 'TFModel02XOR') { new TFModel02XOR(this, 480, 500); }
        if (v === 'TFModel03Iris') { new TFModel03Iris(this, 480, 500); }
        if (v === 'TFModel03SmartDrawing') { new TFModel03SmartDrawing(this, 480, 500); }

        if (v === 'TFModel01HorsepowerAndMilesPerGallon') { new TFModel01HorsepowerAndMilesPerGallon(this, 500, 500); }

        // ...............................................................................
        if (v === 'TFModel01') { new TFModel01(this, 500, 500); }

        if (v === 'JSLinearRegression') { new JSLinearRegression01(this); }
        if (v === 'JSLinearRegressionGD') { new JSLinearRegressionGradientDescent02(this); }
        if (v === 'TFLinearRegressionGD') { new TFLinearRegressionGradientDescent01(this); }

        if (v === 'TFPolynomialRegression GD') { new TFPolynomialRegressionGradientDescent02(this); }
        if (v === 'TFPolyRegress Interaction') { new TFPolynomialRegressionGradientDescent03Interaction(this, 500, 500); }

        if (v === 'TFMnist') { new TFMnist(this, 500, 500); }

        console.log(v);
        // ...............................................................................
    }
    public InitPullDown() {
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel01');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel02XOR');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel03Iris');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@--');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel03SmartDrawing');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel01HorsepowerAndMilesPerGallon');

        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPlayground01');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@JSLinearRegression');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@JSLinearRegressionGD');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFLinearRegressionGD');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@--');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolynomialRegression GD');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolyRegress Interaction');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolyRegress Miles/Gallon');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@--');
        this.implementation.app.viewController.NUIWrapper.NUI.pullDownMenuWrapper.menus[1].AddApp('tfProject@TF Regression');
    }
}