    public static ConvertToTensors(data: number[][], targets: number[], testSplit: number) {
        console.log('++');
        console.log(data);
        console.log(targets);
        console.log(testSplit);

        const numExamples = data.length;
        if (numExamples !== targets.length) {
            throw new Error('data and split have different numbers of examples');
        }

        const numTestExamples = Math.round(numExamples * testSplit);
        const numTrainExamples = numExamples - numTestExamples;

        const xDims = data[0].length;

        // Create a 2D `tf.Tensor` to hold the feature data.
        const xs = tf.tensor2d(data, [numExamples, xDims]);

        // Create a 1D `tf.Tensor` to hold the labels, and convert the number label
        // from the set {0, 1, 2} into one-hot encoding (.e.g., 0 --> [1, 0, 0]).
        const ys = tf.oneHot(tf.tensor1d(targets).toInt(), 4); // IRIS_NUM_CLASSES

        // Split the data into training and test sets, using `slice`.
        const xTrain = xs.slice([0, 0], [numTrainExamples, xDims]);
        const xTest = xs.slice([numTrainExamples, 0], [numTestExamples, xDims]);
        const yTrain = ys.slice([0, 0], [numTrainExamples, 4]); // IRIS_NUM_CLASSES
        const yTest = ys.slice([0, 0], [numTestExamples, 4]); // IRIS_NUM_CLASSES
        return [xTrain, yTrain, xTest, yTest];
    }

