    public static Shuffle(array: number[][]) {
        var currentIndex = array.length, temp, randomIndex;

        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
            // Pick a remaining element...
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;
            // And swap it with the current element.
            temp = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temp;
        }

        return array;
    }
    // https://beta.observablehq.com/@tvirot/machine-learning-in-javascript-with-tensorflow-js#inputData
    public static GetSyntheticDataTensor(totalNumData: number = 200, aCo: number = 0, bCo: number = 0, cCo: number = 0, dCo: number = 0, std: number = 0.2, needYNormal: boolean = false) {

        return tf.tidy(() => {
            const NUM_POINTS = totalNumData;
            // const [a, b, c, d] = coeff.map(i => tf.scalar(i)); // a = tf.scalar(coeff[0]), and so on
            const a = tf.scalar(aCo);
            const b = tf.scalar(bCo);
            const c = tf.scalar(cCo);
            const d = tf.scalar(dCo);

            const x = tf.randomUniform([NUM_POINTS], -1, 1);
            const y = a.mul(x.pow(tf.scalar(3))).add(b.mul(x.square())).add(c.mul(x)).add(d).add(tf.randomNormal([NUM_POINTS], 0, std));

            if (needYNormal) {
                const ymin = y.min();
                const ymax = y.max();
                const theRange = ymax.sub(ymin);
                const yNomalized = y.sub(ymin).div(theRange);
                return { x, yNomalized };
            }

            return [x, y];
        });
    }
    public static GetSyntheticData(totalNumData: number = 200, aCo: number = -0.2, bCo: number = -0.66, cCo: number = -0.1, dCo: number = 2.8, std: number = 0.2, needYNormal: boolean = false) {
        return tf.tidy(() => {
            const result = this.GetSyntheticDataTensor(totalNumData, aCo, bCo, cCo, dCo, std, needYNormal);
            const xx = result[0].dataSync();
            const yy = result[1].dataSync();
            return { xx, yy };
        });
    }
