import * as tf from '@tensorflow/tfjs';
import { TFProject } from '..';
import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { NVector3, MathUtility } from '../../../lib';
import { TFDataUtility } from '../../../../../../lib/NJSCore/ML/NMacineLearning';

// import { TFUtility } from '../../../../../../lib/NJSCore/ML/NMacineLearning';
// https://editor.p5js.org/codingtrain/sketches/UdXRZxF7e
export class TFPolynomialRegressionGradientDescent02 extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];
    vsTemp: NVector3[] = [];
    selVec: NVector3 | undefined = undefined;

    tfLinearRegression: PolynomialRegression;
    t: number = 0.0;
    constructor(tfProjct: TFProject) {
        super(tfProjct);
        this.indeCanvas.title = 'TF LinearRegression GradientDescent 01';
        this.Init();
        this.InitData();
        this.InitUI();
    }
    public InitUI() {
        const div = document.createElement('div');
        // div.style.background = '#dddddd';
        div.style.position = 'absolute';
        div.style.left = '400px';
        div.style.color = '#ffffff';

        const t = document.createElement('p');
        t.textContent = 'click on this screen to add data';
        div.append(t);


        const divHost = document.getElementById('indePanel');
        if (divHost) {
            divHost.append(div);

            const b = document.createElement('button');
            b.textContent = 'run';
            b.style.position = 'absolute';
            b.style.left = '650px';
            b.onclick = () => {
                console.log(this);
            };
            this.scene.implementation.app.model.modelViewCoreWrapper.viewWrapper.div.append(b);
        }
        // this.scene.canvas.canvas.
    }
    InitData() {
        TFDataUtility.getData().then((d: any) => {
            let xMin = Number.MAX_SAFE_INTEGER;
            let xMax = Number.MIN_SAFE_INTEGER;
            let yMin = Number.MAX_SAFE_INTEGER;
            let yMax = Number.MIN_SAFE_INTEGER;

            d.map((data: any) => {
                let x = (data['horsepower']);
                if (x > xMax) { xMax = x; }
                if (x < xMin) { xMin = x; }

                let y = (data['mpg']);
                if (y > yMax) { yMax = y; }
                if (y < yMin) { yMin = y; }
            });

            for (let data of d) {
                let x = (data['horsepower']);
                let y = (data['mpg']);
                this.vsTemp.push(new NVector3(
                    MathUtility.Remap(x, xMin, xMax, -0.9, 0.9),
                    MathUtility.Remap(y, yMin, yMax, -0.9, 0.9)));
            }
        });
    }
    Init() {
        console.log(tf.version);

        this.tfLinearRegression = new PolynomialRegression();
    }
    MouseDown(x: number, y: number) {
        this.vs.push(new NVector3(this.MapX(x), this.MapY(y)));
        // console.log(this.vs);
    }
    MouseDoubleClick() {
        // console.log(this.tfLinearRegression.dim);
        // this.tfLinearRegression.dim++;
    }
    MapX(v: number) {
        return MathUtility.Remap(v, 0, this.width, -1, 1);
    }
    MapY(v: number) {
        return MathUtility.Remap(v, 0, this.height, 1, -1);
    }
    Grid(ctx: CanvasRenderingContext2D) {
        // for (let i = -10; i < 10; ++i) {
        //     for (let j = -10; j < 10; ++j) {
        //         ctx.beginPath();
        //         ctx.fillStyle = 'rgba(255, 0, 0, 0.9)';
        //         ctx.arc(i, j, 0.05, 0, Math.PI * 2);
        //         ctx.closePath();
        //         ctx.fill();
        //     }
        // }
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        this.Grid(ctx);
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

        if ((this.t & 2) === 0) {
            const v = this.vsTemp.pop();
            if (v) {
                this.vs.push(v);
            }
        }

        // this.GradientDescent();
        if (this.vs.length > 1) {
            const xx = [], yy = [];
            for (let i = 0; i < this.vs.length; ++i) { xx.push(this.vs[i].x); yy.push(this.vs[i].y); }
            // console.log(xx);
            // console.log(yy);
            // ............................ train
            this.tfLinearRegression.train(xx, yy);
            // ............................ end train

            this.RenderLine(ctx);
        }

        ctx.fillStyle = '#00ff00';
        for (let i = 0; i < this.vs.length; ++i) {
            ctx.beginPath();
            ctx.arc(
                MathUtility.Remap(this.vs[i].x, -1, 1, 0, this.width),
                MathUtility.Remap(this.vs[i].y, -1, 1, this.height, 0),
                5, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
        this.t += 0.3;
    }
    public RenderLine(ctx: CanvasRenderingContext2D) {
        const curveX: number[] = [];
        for (let x = -1; x <= 1; x += 0.05) {
            curveX.push(x);
        }

        // ............................ predict
        const ys = tf.tidy(() => {
            // return this.tfLinearRegression.predict(tf.tensor1d(curveX));
            return this.tfLinearRegression.predict2d(tf.tensor1d(curveX));
        });

        let curveY = ys.dataSync();
        ys.dispose();
        // ............................ end predict

        ctx.lineWidth = 3;
        ctx.strokeStyle = '#00fff0';
        ctx.beginPath();
        ctx.moveTo(MathUtility.Remap(curveX[0], -1, 1, 0, this.width), MathUtility.Remap(curveY[0], -1, 1, this.height, 0));

        for (let i = 1; i < curveX.length; ++i) {
            ctx.lineTo(MathUtility.Remap(curveX[i], -1, 1, 0, this.width), MathUtility.Remap(curveY[i], -1, 1, this.height, 0));
        }
        ctx.stroke();

    }
}
class PolynomialRegression {
    weights: tf.Variable[] = [];
    bias: tf.Variable;

    dim: number = 1;
    leraningRate: number = 0.2;
    optimizer: tf.AdamOptimizer;
    constructor() {
        for (let i = 0; i < 5; ++i) {
            const weight = tf.variable(tf.scalar((Math.random() * 2) - 1));
            weight.print();
            this.weights.push(weight);
        }

        this.bias = tf.variable(tf.scalar((Math.random() * 2) - 1));
        this.optimizer = tf.train.adam(this.leraningRate);
    }

    public predict1d(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        // y = ax^3 + bx^2 + cx + d
        const ys = x.mul(this.weights[0])
            .add(this.bias);
        return ys;
    }
    public predict2d(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        // y = ax^3 + bx^2 + cx + d
        const ys = x.square().mul(this.weights[1])
            .add(x.mul(this.weights[0]))
            .add(this.bias);
        return ys;
    }
    public predict3d(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        // y = ax^3 + bx^2 + cx + d
        const ys = x.pow(tf.scalar(3)).mul(this.weights[2])
            .add(x.square().mul(this.weights[1]))
            .add(x.mul(this.weights[0]))
            .add(this.bias);
        return ys;
    }
    public predict4d(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        // y = ax^3 + bx^2 + cx + d
        const ys = x.pow(tf.scalar(4)).mul(this.weights[3])
            .add(x.pow(tf.scalar(3)).mul(this.weights[2]))
            .add(x.square().mul(this.weights[1]))
            .add(x.mul(this.weights[0]))
            .add(this.bias);
        return ys;
    }
    public predict5d(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        // y = ax^3 + bx^2 + cx + d
        const ys = x.pow(tf.scalar(5)).mul(this.weights[4])
            .add(x.pow(tf.scalar(4)).mul(this.weights[3]))
            .add(x.pow(tf.scalar(3)).mul(this.weights[2]))
            .add(x.square().mul(this.weights[1]))
            .add(x.mul(this.weights[0]))
            .add(this.bias);
        return ys;
    }
    public predict(x: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        if (this.dim === 5) { return this.predict5d(x); }
        if (this.dim === 4) { return this.predict4d(x); }
        if (this.dim === 3) { return this.predict3d(x); }
        if (this.dim === 2) { return this.predict2d(x); }

        return this.predict1d(x);
    }
    public loss(pred: tf.Tensor<tf.Rank>, label: tf.Tensor<tf.Rank>): tf.Tensor<tf.Rank> {
        return pred.sub(label).square().mean();
    }
    public train(xVal: number[], yVal: number[]) {

        tf.tidy(() => {
            const xs = tf.tensor1d(xVal);
            const ys = tf.tensor1d(yVal);
            this.optimizer.minimize((): any => this.loss(this.predict(xs), ys));
        });
        console.log(tf.memory().numTensors);
        // console.log('b:', this.bias, ', w:', this.weight);
        // this.bias.print();
        // this.weight.print();
    }
    // public train(x: tf.Tensor, y: tf.Tensor) {
    //     tf.tidy((): any => {
    //         return this.optimizer.minimize((): any => {
    //             return this.loss(this.predict(x), y);
    //         });
    //     });
    // }
}